body {background: #111111;}
body.open {overflow: hidden;}

.wrapper {
	width: 100%;
	max-width: 1920px;
	padding: 0 30px;
	margin: 0 auto;
}

.wrapper-m {max-width: 1703px;}
.wrapper-m {max-width: 1980px;}

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	max-width: 272px;
	height: 66px;
	padding: 0 15px;
	font-family: 'Open Sans Light';
	font-size: 21px;
	text-align: center;
	color: #ffffff;
	border-radius: 33px;
	transition: 0.3s ease;
}

.btn-red {background-color: #b71c1c;}
.btn-red:hover {background-color: #880000;}

.btn-blue {background-color: #263e88;}
.btn-blue:hover {background-color: #011c53;}

.btn-small {
	max-width: 178px;
	font-size: 16px;
	height: 43px;
}

.btn-nobg {
	width: auto;
	max-width: 100% !important;
	height: 41px;
	padding: 0 24px;
	font-size: 18px;
	color: #ffffff;
	border: 1px solid #ffffff;
	background-color: transparent;
	border-radius: 21px;
	&:not(.active):hover {
		color: #000000;
		background-color: #ffffff;
	}
	&.active {
		background-color: #263e88;
		border: 1px solid #263e88;
	}
}

.w-100 {width: 100%;}


header {
    display: block;
	width: 100%;
	padding: 21px 0 18px;
	background: #111111;
	box-shadow: 0 0 50px #000000;
}

header .wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.triggers-case,
.lang-active,
.apartament-book-active  {
	display: flex;
	align-items: center;
	justify-content: center;
}

.lang-trigger,
.apartament-book-trigger {
	position: relative;
	// z-index: 3;
	margin-right: 18px;
	padding: 15px;
	cursor: pointer;
	transition: 0.3s ease;
}

.lang-trigger a, .lang-active,
.apartament-book-trigger a, .apartament-book-active {
	font-family: 'Open Sans Light';
	font-size: 21px;
	line-height: 21px;
	color: #ffffff;
	transition: 0.3s ease;
}

.apartament-book-trigger a, .apartament-book-active {
	font-size: 18px;
	line-height: 18px;
}

.lang-trigger a:not(:last-child),
.apartament-book-trigger a:not(:last-child) {margin-bottom: 13px;}

.lang-trigger a:hover,
.apartament-book-trigger a:hover {color: #b71c1c;}

.lang-active img,
.apartament-book-trigger img{
	width: 100%;
	max-width: 9px;
	margin-left: 13px;
	transition: 0.3s ease;
}

.lang-select,
.apartament-book-select {
	position: absolute;
	top: 100%;
	left: 0;
	z-index: -1;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	width: 100%;
	padding-bottom: 15px;
	box-shadow: 0 3px 5px #000000;
	background-color: #111111;
	opacity: 0;
	transition: 0.3s ease;
}

.login-trigger {
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: 'Open Sans Light';
	font-size: 21px;
	line-height: 21px;
	color: #ffffff;
	transition: 0.3s ease;
}

.login-trigger svg {
	min-width: 29px;
	max-width: 29px;
	margin-right: 13px;
}

.login-trigger svg .st0 {transition: 0.3s ease;}

.login-trigger:hover {color: #b71c1c;}

.login-trigger:hover svg .st0 {fill: #b71c1c;}



@media(min-width: 1201px) {
	.lang-trigger:hover,
	.apartament-book-trigger:hover {
		box-shadow: 0 0 15px #000000;
		background: #111111;
	}
	.lang-trigger:hover .lang-active img,
	.apartament-book-trigger:hover .apartament-book-active img {transform: rotate(180deg);}
	.lang-trigger:hover .lang-select,
	.apartament-book-trigger:hover .apartament-book-select {
		opacity: 1;
		z-index: 6;
	}
}

@media(max-width: 1200px) {
	.lang-trigger.open,
	.lang-trigger.open,
	.apartament-book-trigger.open {
		box-shadow: 0 0 15px #000000;
		background: #111111;
	}
	.lang-trigger.open .lang-active img,
	.apartament-book-trigger.open  .apartament-book-active img {transform: rotate(180deg);}
	.lang-trigger.open .lang-select,
	.apartament-book-trigger.open .apartament-book-select {
		opacity: 1;
		z-index: 8;
	}
}

.profile-link {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 15px;
	cursor: pointer;
	// transition: 0.3s ease;
}
.profile-link a {
	display: block;
	width: 100%;
	font-family: 'Open Sans Light';
	font-size: 21px;
	line-height: 21px;
	color: #ffffff;
	text-align: left;
	transition: 0.3s ease;
}
.profile-link-cnt a:not(:last-child) {margin-bottom: 13px;}

.profile-link a.exit {color: #4c4c4c;}

.profile-link-cnt {
	position: absolute;
	top: 100%;
	left: 0;
	z-index: -1;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	width: 100%;
	padding: 0 15px 15px;
	box-shadow: 0 3px 5px #000000;
	background-color: #111111;
	opacity: 0;
	transition: 0.3s ease;
}

.profile-link-cnt a:hover {color: #b71c1c;}

@media(min-width: 768px) {
	.profile-link:hover,
	.profile-link:hover {
		box-shadow: 0 0 15px #000000;
		background: #111111;
	}
	.profile-link:hover .profile-name {border-bottom: 1px solid #ffffff;}
	.profile-link:hover .profile-link-cnt {
		opacity: 1;
		z-index: 6;
	}
}

.profile-link.open {
	box-shadow: 0 0 15px #000000;
	background: #111111;
	& .profile-name {border-bottom: 1px solid #ffffff;}
	& .profile-link-cnt {
		opacity: 1;
		z-index: 6;
	}
}


.profile-name {
	margin-right: 10px;
	font-family: 'Open Sans light';
	font-size: 21px;
	line-height: 21px;
	color: #ffffff;
	border-bottom: 1px solid transparent;
	transition: 0.3s ease;
}

.profile-link svg {
	width: 23px;
	height: 23px;
}

.cart {
	position: relative;
	margin-left: 33px;
}

.cart svg {
	width: 40px;
	height: 37px;
}

.profile-link svg .st0,
.cart svg .st0 {
	fill: #ffffff;
	transition: 0.3s ease;
}

.cart:hover svg .st0 {fill: #b71c1c;}

.cart span {
    position: absolute;
    bottom: -6px;
    right: -9px;
