    -webkit-box-orient: vertical;
}


.left-sidebar {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 5;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 340px;
    height: 100%;
    padding: 53px 0 15px;
	background: rgba(0, 0, 0, 0.72);
}

.left-sidebar.active {
	position: fixed;
	background-color: #111111;
	height: 100vh;
	box-shadow: 0 0 30px #000000;
}


.left-sidebar .btn {margin: 0 auto 145px;}

.left-sidebar-menu {
    width: 100%;
    padding: 0;
	margin: 0;
	list-style: none;
}


@media(min-width: 1201px) {.left-sidebar-menu li:hover > ul {display: block;}}

.left-sidebar-menu li > ul.open  {display: block;}

.left-sidebar-menu > li {width: 100%;}

.left-sidebar-menu > li:not(:last-child) .left-sidebar-link {position: relative;}

.left-sidebar-menu > li:not(:last-child) .left-sidebar-link::before {
	content: '';
